<template>
  <q-card class="rounded" style="width: 500px; max-width: 80vw">
    <q-toolbar class="bg-primary text-white">
      <GrowmodoTextIcon size="md" :text="false" :dark="true" />
      <q-toolbar-title class="text-h1 font-medium" style="font-size: 1.25rem">
        Login to {{ $config?.pkjson?.productName || 'Growmodo Hub' }}
      </q-toolbar-title>
    </q-toolbar>
    <q-form @submit="loginWithPwd" ref="authorization_form" class="q-pa-md">
      <div>
        <q-input
          dense
          outlined
          autofocus
          clearable
          lazy-rules
          autocomplete
          tabindex="1"
          clear-icon="icon-x-close"
          placeholder="Username or email"
          v-model="u_useridentity"
          :input-class="$config?.class?.activeInput"
          :rules="[(val) => checker.input(!!val || '', 250)]"
          @keyup="modKey"
          class="rounded"
        >
          <template v-slot:prepend>
            <q-icon size="xs" name="icon-user-03" />
          </template>
        </q-input>
      </div>
      <div>
        <InputPassword
          dense
          withicon
          outlined
          clearable
          lazy-rules
          autocomplete
          tabindex="2"
          placeholder="Password"
          clear-icon="icon-x-close"
          v-model="u_password"
          :input-class="$config?.class?.activeInput"
          :rules="[(val) => checker.input(!!val || '', 250)]"
          @keyup="modKey"
          class="rounded"
        />
      </div>
      <div class="row q-gutter-md">
        <q-space />

        <q-btn
          outline
          no-caps
          unelevated
          tabindex="3"
          :ripple="{ color: 'accent' }"
          color="primary"
          class="rounded slide-hover bg-none"
          to="/signup"
          :disable="decrypting.status"
        >
          <span class="q-px-sm">Signup</span>
        </q-btn>

        <q-btn
          no-caps
          unelevated
          tabindex="3"
          :ripple="{ color: 'accent' }"
          color="primary"
          class="rounded slide-hover"
          :disable="decrypting.status"
          :percentage="decrypting.percentage"
          @click="submitValidation"
        >
          <span class="q-pl-sm">Login</span>
          <q-icon
            size="sm"
            name="icon-arrow-narrow-right"
            class="q-px-sm slide-hover-left-md"
          />

          <template v-slot:loading>
            <q-spinner size="sm" class="on-left" v-if="decrypting.status" />
            Verifying
          </template>
        </q-btn>
      </div>
    </q-form>
  </q-card>
</template>

<script>
import { defineComponent } from 'vue';
import {
  defaultModKey,
  checker,
  tryClearInterval,
} from 'src/assets/scripts/functions';
import GrowmodoTextIcon from 'src/components/GrowmodoTextIcon.vue';
import InputPassword from 'src/components/Helpers/InputPassword.vue';
import { mapActions } from 'pinia';
import useUser from 'stores/user';

export default defineComponent({
  name: 'AuthenticationForm',

  components: { GrowmodoTextIcon, InputPassword },

  props: {
    allowSignup: {
      type: Boolean,
      default: true,
    },
  },

  data() {
    return {
      checker,
      loginAnimate: null,
      u_useridentity: null,
      u_password: null,
      interval: null,
      login_response: null,
      decrypting: { status: false, percentage: 0 },
    };
  },

  watch: {
    login_response: {
      async handler(val) {
        if (val) {
          tryClearInterval(this.loginAnimate);
          setTimeout(() => {
            this.decrypting.status = false;
          }, 500);
        }
      },
    },
  },

  emits: [
    'verify_email',
    'success',
    'failed',
    'loading',
    'pending',
    'onSignup',
  ],

  methods: {
    ...mapActions(useUser, [
      'loginWithPassword',
      'isEmailVerified',
      'onSignupStep',
    ]),

    submitValidation() {
      this.$refs?.authorization_form?.submit?.();
    },

    async loginWithPwd() {
      this.loginLoading();
      this.login_response = null;
      const data = {
        username: this.u_useridentity,
        password: this.u_password,
      };
      this.$emit('loading', data);
      // Send Request to API
      const response = await this.loginWithPassword(data);
      this.login_response = response || {};
      // Emit Login Response
      const email_verified = this.isEmailVerified(this.login_response);
      const onSignup = this.onSignupStep(
        this.login_response.data?.user?.signup_step
      );
      if (this.login_response?.success) {
        if (this.login_response?.data?.user?.status === 'pending') {
          this.$emit('pending', this.login_response?.data);
        } else if (!email_verified) {
          this.$emit('verify_email', this.login_response?.data?.user);
        } else if (onSignup) {
          this.$emit('onSignup', onSignup);
        } else {
          this.$emit('success', this.login_response || {});
        }
      } else {
        this.$emit('failed', this.login_response || {});
      }
    },

    async loginLoading(callback) {
      this.decrypting.status = true;
      this.loginAnimate = setInterval(() => {
        this.decrypting.percentage += Math.floor(Math.random() * 8 + 10);
        if (this.decrypting.percentage >= 100) {
          if (callback) callback();
          this.decrypting.status = false;
          tryClearInterval(this.loginAnimate);
        }
      }, 1000);
    },

    /**
     * Modify Enter Key for Inputs
     * @param {KeyboardEvent} evt event object
     */
    async modKey(evt) {
      defaultModKey(evt, this.submitValidation);
    },
  },
});
</script>
